}
img {
  width: 100%;
}
.log-container {
  background: #2e2e2e;
  padding: 30px;
  border-radius: 10px;
}
.log-title {
  font-size: 20px;
  font-weight: 500;
  letter-spacing: 2px;
  color: #fff;
  text-align: left;
  line-height: 2.5;
  margin-bottom: 20px;
  border-bottom: 1px solid #1f1f1f;
}
.log-content {
  display: flex;
  align-items: flex-start;
  margin-bottom: 30px;
}
.log-photo {
  width: 70px;
  border-radius: 50%;
  overflow: hidden;
  margin-right: 3%;
}
.log-text {
  width: 70%;
  min-height: 70px;
  padding: 15px;
  border: 1px solid #1f1f1f;
  border-radius: 5px;
  position: relative;
  box-shadow: 3px 3px 3px rgba(0, 0, 0, 0.2);
}
.triangle {
  display: inline-block;
  width: 0;
  height: 0;
  border: 8px solid transparent;
  border-right-color: #2e2e2e;
  position: absolute;
  top: 27px;
  left: -16px;
}
.triangle:first-child {
  left: -17px;
  border-right-color: #1f1f1f;
}
.journal {
  font-size: 16px;
  line-height: 1.5;
  letter-spacing: 1px;
}
.text-title {
  color: #fff;
  line-height: 2;
}
.text-content {
  color: #a6a6a6;
  margin-left: 20px;
}
.text-time {
  font-size: 14px;
  margin-top: 15px;
  text-align: right;
}
.text-time span {
  margin-right: 10px;
}
@media (max-width: 750px) {
  .log-container {
    padding: 15px;
  }
  .log-content {
    flex-direction: column;
  }
  .log-photo {
    width: 60px;
    border-radius: 50%;
    overflow: hidden;
    margin-bottom: 20px;
  }
  .log-text {
    width: 100%;
  }
  .triangle {
    border-right-color: transparent;
    border-bottom-color: #2e2e2e;
    top: -16px;
    left: 22px;
  }
  .triangle:first-child {
    top: -17px;
    left: 22px;
    border-right-color: transparent;
    border-bottom-color: #1f1f1f;
  }
  .journal {
    font-size: 14px;
  }
  .text-time {
    font-size: 12px;
    margin-top: 10px;
  }
}
</style>



