<template>
  <div class="intro">
    <div class="left-img">
      <div class="bg-img">
        <img src="/static/images/intro3.png" alt />
        <div class="cover">
          <div class="hobby-text" >
            <p></p>
            <p> </p>
            <p>   </p>
            <p> </p>
            <p> </p>
          </div>
          <div class="phone-hobby-text">
            <p></p>
            <p></p>
            <p></p>
            <p></p>
            <p></p>
          </div>
        </div>
      </div>
      <div class="bg-img">
        <img src="/static/images/intro2.png" alt />
        <div class="cover">
          <div class="hobby-text" >
            <p></p>
            <p></p>
          </div>
          <div class="phone-hobby-text">
            <p></p>
            <p></p>
          </div>
        </div>
      </div>
    </div>
    <div class="right-img">
      <div class="bg-img">
        <img src="/static/images/intro1.png" alt />
        <div class="cover">
          <div class="intro-text">
            <p></p>
            <p> </p>
            <p>19960908</p>
            <p> </p>
            <p> </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: "home",
  data() {
    return {};
  },
  props: {
    //
    map: {
      //type:String
    }
  },
  methods: {
    //
    //clickdrawFeatures
  },
  created() {
    //
  },
  mounted() {
    //
    $(".bg-img").bind("mouseenter mouseleave", function(e) {
      var this_slidiv = $(this).find(".cover");
      var offset = $(this).offset();
      var w = $(this).width(); //silde
      var h = $(this).height(); //slide
      var x = e.pageX; //
      var y = e.pageY; //
      var l = offset.left; //slide
      var t = offset.top; //slide
      var a = Math.abs(x - l); //slide
      if (a == 0) {
        a = 1;
      }
      var b = Math.abs(y - t); //slide
      if (b == 0) {
        b = 1;
      }
      var n = 1; //     1 2 3 4
      if (a / b > w / h) {
        //n = 1 || 2
        if (Math.abs(w - a) / Math.abs(b) > w / h) {
          n = 1;
        } else {
          n = 2;
        }
      } else {
        //n = 3 || 4
        if (Math.abs(w - a) / Math.abs(b) > w / h) {
          n = 4;
        } else {
          n = 3;
        }
      }
      //console.log(n)
      //console.log(b)
      if (e.type == "mouseenter") {
        switch (n) {
          case 1:
            this_slidiv.css({
              top: -h,
              left: "0px"
            });
            break;
          case 2:
            this_slidiv.css({
              top: "0px",
              left: w
            });
            break;
          case 3:
            this_slidiv.css({
              top: h,
              left: "0px"
            });
            break;
          case 4:
            this_slidiv.css({
              top: "0px",
              left: -w
            });
            break;
        }

        this_slidiv.stop(true, true).animate(
          {
            top: "0px",
            left: "0px"
          },
          300
        );
      } else if (e.type == "mouseleave") {
        switch (n) {
          case 1:
            this_slidiv.stop(true, true).animate(
              {
                top: -h
              },
              300
            );
            break;
          case 2:
            this_slidiv.stop(true, true).animate(
              {
                left: w
              },
              300
            );
            break;
          case 3:
            this_slidiv.stop(true, true).animate(
              {
                top: h
              },
              300
            );
            break;
          case 4:
            this_slidiv.stop(true, true).animate(
              {
                left: -w
              },
              300
            );
            break;
        }
      }
    });
  }
};
</script>

<style scoped>
img {
  width: 100%;
}

.intro {
  margin: 50px 0 20px;
  position: relative;
}
.left-img {
  width: 32%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  position: absolute;
  top: 0;
  right: 0;
}
.right-img {
  width: 66%;
}
.bg-img {
  position: relative;
  overflow: hidden;
  border-radius: 4px;
  box-shadow: 5px 5px 10px rgba(0, 0, 0, 0.2);
}
.cover {
  color: #fff;
  width: 100%;
  height: 100%;
  background: rgba(29, 29, 29, 0.5);
  text-align: center;
  position: absolute;
  top: 0;
  left: -100%;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
.intro-text {
  width: 75%;
  height: 70%;
  text-align: left;
  font-size: 24px;
  line-height: 2;
  letter-spacing: 1px;
  overflow: hidden;
}
.intro-text p:first-child {
  font-size: 35px;
  margin-bottom: 10px;
}
.intro-text p:not(:first-child){
  text-indent: 48px;
  white-space: nowrap;
}
.hobby-text {
  display: block;
  width: 85%;
  height: 85%;
  text-align: left;
  font-size: 16px;
  letter-spacing: 1px;
  overflow: hidden;
}
.hobby-text p:first-child {
  font-size: 25px;
  margin-bottom:5px;
  line-height: 2;
}
.hobby-text p:not(:first-child){
  text-indent: 40px;
  margin-bottom: 5px;
  line-height: 1.5;
}
.phone-hobby-text{
  display: none;
}
@media (max-width: 750px) {
  .left-img {
    width: 100%;
    flex-direction: row;
    position: relative;
    margin-bottom: 2%;
  }
  .left-img .bg-img {
    width: 49%;
  }
  .right-img {
    width: 100%;
  }
  .intro {
    margin: 10px 0;
  }
  .intro-text {
    width: 80%;
    height: 80%;
    font-size: 18px;
  }
  .intro-text p:first-child {
    font-size: 24px;
    margin-bottom: 5px;
  }
  .intro-text p:not(:first-child){
    text-indent: 30px;
  }
  .hobby-text{
    display: none;
  }
  .phone-hobby-text{
    display: block;
    width: 85%;
    height: 85%;
    text-align: left;
    font-size: 14px;
    letter-spacing: 1px;
    overflow: hidden;
  }
  .phone-hobby-text p:first-child {
    font-size: 17px;
    line-height: 2;
  }
  .phone-hobby-text p:not(:first-child){
    text-indent: 28px;
    line-height: 1.5;
  }
}
</style>



